@font-face {
	font-family: Halo;
	src: url("media/halo.fa7c9c77.otf");
}
@font-face {
	font-family: geomanist;
	src: url("media/geomanist.b9d043c5.otf");
}
button {
	margin: 5rem 2rem;
}
h1 {
	-webkit-animation: bounce;
	animation: bounce;
	-webkit-animation-duration: 500s;
	animation-duration: 500s;
	font-family: Halo;
	font-size: 8rem;
	word-spacing: 1rem;
}
h1,
h2 {
	font-weight: 100;
}
h2 {
	font-family: geomanist;
	font-size: 5rem;
	text-align: right;
}
@media only screen and (max-device-width: 1100px) {
	h1 {
		font-size: 5rem;
	}
	h2 {
		font-size: 3rem;
	}
}
